.portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
}
.portfolio span:nth-of-type(1){
    font-size: 2rem;
    font-weight: bold;
    
}
.portfolio span:nth-of-type(2){
    font-size: 2.5rem;
    font-weight: bold;
    color: blue;
    
}
.portfolio .swiper{
    overflow: visible!important;

}
.portfolio-slider{
    margin-top: 3rem;
    width: 100%;

}
.portfolio-slider .swiper-slider
{
    width: 20rem;
}
.portfolio img{
    width: 19rem;
    filter:drop-shadow(-12px 15px 13px rgba(0,0,0,0.25));
    border-radius: 19px;
}

@media  screen and (max-width:480px) {
.portfolio
{
    margin-top: 25rem;
    flex-direction: column;
}
.portfolio img{
    width: 19rem;
    filter:drop-shadow(-12px 15px 13px rgba(0,0,0,0.25));
    border-radius: 19px;
}
.t
{
    transform: scale(0.2);
}

}